
@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300..700&display=swap');
* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   font-family: 'Comfortaa',sans-serif;
}

body {
   display: flex;
   flex-direction: column;
   margin-top: 400px;
   font-family: Arial, sans-serif;
   background-color: #e6f0ff;
   min-height: 100vh;
}

.container {
   padding: 20px;
}

h1 {
   text-align: center;
   margin-bottom: 20px;
}

.content {
   display: flex;
   justify-content: space-between;
   
}

.feed {
   position: absolute;
   width: 100%;
   background-color: #7fc8f8; 
}

.sidebarright {
   position: absolute;
   top: -600px;
   right: -380px;
   width: 32%;
   background-color: #7fc8f8;
   height: 100%;
   min-height: 100vh;
   max-height:650px;
   padding: 10px;
   border-radius: 10px;
   border: 1px solid #ccc;
  
}

.post {
   background-color: #D9D9D9;
   border-radius: 10px;
   box-shadow: 0 0 10px rgba(0,0,0,0.1);
   margin-top: 20px;
   margin-left: 100px;
   margin-right: 100px;
   margin-bottom: 20px;
   padding: 20px;
}

.post-header {
   display: flex;
   align-items: center;
   margin-bottom: 10px;
   margin-top: -5px;
   position: relative;
}

.photo img {
   width: 100%;
   max-width: 80px;
   height: 80px;
   border-radius: 100%;
   margin-right: 10px;
   object-fit: fill;
   border: 2px solid #a43ab2;
}

.user-info {
   flex-grow: 1;
}

.username {
   display: block;
   font-weight: bolder;
   margin-left: 10px;
   font-style: normal;
   font-size: 20px;
   cursor: pointer;

}
.username:hover{
   text-decoration: underline blue;
   
}
.username a{
   color: inherit;
   text-decoration: none;
}

.time{
   display: flex;
   margin-left: 10px;
   margin-top: 5px;

  
}

.clock{
   box-sizing:border-box;
   position:relative;
   display:block;
   transform:scale(var(--ggs,1));
   width: 15px;
   height: 15px;
   border-radius:100%;
   border: 2px solid transparent;
   box-shadow: 0 0 0 2px currentColor
}
.clock::after{
   content: "";
    display: block;
    box-sizing: border-box;
    position: absolute;
    width: 7px;
    height: 7px;
    border-left: 2px solid;
    border-bottom: 2px solid;
    top: 1px;
    left: 5px
}

.posttime {
   
   color: #888;
   font-size: 0.9em;
   margin-left: 10px;
}

.menu {
   width: 100%;
   max-width: 40px;
   height: 100%;
   max-height: 25px;
   cursor: pointer;
   font-size: 1.5em;
   margin-top: -30px;
   margin-right: -5px;
   color: white;
   background-color: black;
   border-radius: 50%;
   padding-left: 12px;
   padding-bottom: 40px;
   position: relative;
}

.dropdown {
   display: none;
   position: absolute;
   top: 100%;
   right: 0;
   width: max-content;
   background-color: #464343;
   padding: 10px;
   border-radius: 5px;
   box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
   z-index: 100;
}
.menu:hover .dropdown {
   display: block;
}
.dropdown li {
   list-style: none;
   margin-bottom: 5px;
  
}

.dropdown ion-icon{
   
   padding-right:15px;
   vertical-align:middle;
   padding-bottom: 5px;
   

}
.dropdown li a {
   text-decoration: none;
   color: #F9F9F9;
   padding: 10px 10px;
   display: block;
   border-radius: 3px;
   
   
   
}

.dropdown li a:hover {
   background-color: #b1abab;
}


.post-content {
   text-align: center;

}

.post-image {
   width: 100%;
   border-radius: 10px;
}

.post-interactions {
   position: relative;
   display: flex;
   gap: 20px;
   align-items: center;
   margin: 3px 0;
   
}

.post-interactions button{
   background: none;
   border: none;
   font-size: 1em;
   cursor: pointer;
   display:flex;
   justify-content:flex-start;
   align-items:center;
}
.reaction{
   display: flex;
}
.post-interactions #love {
   font-size: 1.5em;
   color: #333;

}
.heart-fill{
   width:60px;
   height: 45px;
   

}
.heart-fill.filled{
fill: red;
}
#reaction_count {
   font-size: 18px;
   margin: 15px;
   color: #333;
   margin-left: -3px;
 }
#share img {
    width: 100%;
    max-width: 70px;
    height: 60px;
    padding-bottom: 11px;
    object-fit:fill;
    
    
 }
.sharedown{
   display: none;
   position: absolute;
   bottom: 100%;
   width: max-content;
   background-color: #464343;
   padding: 10px;
   border-radius: 5px;
   box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
   z-index: 100;
   text-align: left;
}
#share:hover .sharedown{
      display: block;
}

.sharedown li {
   list-style: none;
   margin-bottom: 5px;
   text-align: left;
}

.sharedown ion-icon{
   width: 30px;
   height: 25px;
   padding-right:15px;
   vertical-align:middle;
   padding-bottom: 5px;
}
.sharedown li a {
   text-decoration: none;
   color: #F9F9F9;
   padding: 10px 10px;
   display: block;
   border-radius: 3px;
   text-align: left; 
}
.sharedown li a:hover {
   background-color: #b1abab;
}

#comment img{
   width: 100%;
   max-width: 60px;
   height: 40px;
   object-fit: fill;
}
#comment_count{
   font-size: 18px;
   margin: 15px;
   color: #333;
   margin-left: -3px;
}
.comment_Section {
   margin-top: 10px;
   position: relative;
 }

 .comment_input {
   width: 100%;
   height: 40px;
   display: flex;
   align-items: center;
   margin-bottom: 10px;
   background: whitesmoke;
   border: 1px solid #000000;
   border-radius: 5px;
   box-sizing: border-box;
 }
 .comment_input:hover input{
   width: 100%;
}
 
 .comment_input input {
   width: 100%;
   height: 35px;
   padding: 8px;
   outline: 0;
   background:transparent;
   margin-right: 8px;
   border-radius: 4px;
   border: none;
   flex: 1;
 }

 .comment_input button {
   background-color: #007bff;
   color: white;
   height: 40px;
   padding: 8px 16px;
   margin-right: -1px;
   border: none;
   border-radius: 5px;
   border-top-left-radius: 0px;
   border-bottom-left-radius: 0px;
   cursor: pointer;
 }
 .comment_input button:hover {
   background-color: #0056b3;
 }
 .comments-list .comment {
   padding: 8px;
   border-bottom: 2px solid rebeccapurple;
 }
 .comment {
   background-color: #f9f9f9;
   display: flex;
   gap: 20px;
   padding: 8px;
   border-radius: 4px;
   margin-bottom: 5px;

 }
 .comment span{
   list-style: none;
   margin-top: 15px;
   margin-left: 5px;
 }
 .comment img{
   width: 100%;
   max-width: 50px;
   height: 50px;
   border-radius: 100%;
   margin-right: 5px;
   object-fit: fill;
   border: 2px solid #a43ab2;
 }


.post-description {
   padding: 10px 0;
}
.aba{
   display:flex;
   gap: 25px;

}
.rating_section {
   display: flex;
   justify-content: center;
   align-items: center;
   margin-top: -9px;
   font-size: 24px;
}
.star {
   cursor: pointer;
   color: white;
   transition: color 0.2s;
}
.star.active{
   color: blue;
}
#ratingValue{
   font-size: 13px;
   font-weight: lighter;
}
.post-description p {
   color: #555;
}

.advertisement {
   
   height: 200px;
   background-color: #ccc;
   margin-bottom: 10px;
   border-radius: 10px;
   display: flex;
   justify-content: center;
   align-items: center;
}
.advertisement img{
   width: 100%;
   height: 100%;
   object-fit: fill;
}

.suggestions {
   margin-bottom: 10px;
   padding: 5px;
   flex-direction: column;
}

.suggestions h3 {
   font-size: 1em;
   margin-bottom: 10px;
   
}

.suggestion {
   display: flex;
   align-items: center;
   margin-bottom: 10px;
   padding: 5px;
}
.name{
   padding: 10px;
   margin-bottom: 10px;
   font-size: large;
   font-weight: bold;

}

.suggestion img {
   width: 100%;
   max-width: 60px;
   height: 100%;
   max-height: 50px;
   border-radius: 50%;
   
}
#Follow{
   margin-left: auto;
 
   margin-bottom: 10px;
   border: none;
   background: transparent;
   color: blue;

}
#Follow:hover{
   color: red;
}
.footer {
   font-size: 0.8em;
   color: black;
   text-align: center;
   padding: 20px;
   display: flex;
   gap: 10px;
}

.footer a {
   color: inherit;
   text-decoration: none;
   margin-right: 5px;
   
}

.footer a:hover {
   color: blue;
}
